import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import avatar from "../assets/avatar.webp";

import "./scss/About.scss";
import CustomTab from "./CustomTab";

function AboutMe() {

  return (
    <div id="about" className="about-me">
      <Container>
        <Row>
          <Col lg={5} md={5}>
            <div className="img-wrapper">
              <img src={avatar} alt="Mahmoud Essam" loading="lazy" />
            </div>
          </Col>
          <Col lg={7} md={7}>
            <div className="info-wrapper">
              <section className="info-header">
                <h2>About Me</h2>
                <p>
                  My Name is Mahmoud, i'm a Fullstack Web Developer
                  i shifted my career from accounting to web development,
                  i graduated from <br /><a href="https://www.iti.gov.eg/iti/home" target="_blank" rel="noreferrer">ITI</a> - Fullstack web development using MEARN...
                  i did some projects to proof to myself before anyone that
                  i'm really good in web development... so feel free
                  to see my projects and wish me luck.
                </p>
              </section>
              <section className="the-info">
                <div className="info-nav-wrapper">
                  <div className="tabs-section">

                    <CustomTab />

                  </div>
                </div>
              </section>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default AboutMe;
